body {
    background-image: url('prejvixd.gif');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: 100% 100%;
}

.acookie {
    position: fixed;
    background-color: rgb(28, 28, 28);
    width: 100%;
    height: 100%;
}

.acookiehide {
    display: none;
}

.acookie img {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

.acookie a {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: bold;
    font-family: Montserrat;
    font-size: 50px;
    margin-top: 20px;
}

.acookie p {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: bold;
    font-family: Montserrat;
    font-size: 24px;
    margin-top: 20px;
    font-weight: normal;
    width: 40%;
}

.acookie h1 {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: bold;
    font-family: Montserrat;
    font-size: 13px;
    margin-top: 20px;
}

.acookie button {
    position: relative;
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    font-weight: bold;
    font-family: Montserrat;
    font-size: 15px;
    margin-top: 50px;
    border: 3px solid rgb(255, 182, 12);
    padding: 10px 15px;
    cursor: pointer;
    border-radius: 10px;
    background-color: #0000;
    color: white;
    transition: 300ms;
}

.acookie button:hover {
    border: 3px solid rgba(255, 255, 255, 0);
    background-color: rgb(255, 182, 12);
    transition: 300ms;
}